body {
  background-color: #000000;
}

.App {
  margin-top: 50px;
  font-family: sans-serif;
  text-align: center;
  color: #ffffff;
}

.dataBox {
  height: 110px;
}

input {
  width: 100px;
  margin: 0 0 0 15px;
  font-size: 14px;
  text-align: center;
  color: white;
  background-color: transparent;
  border: 1px solid transparent;
  border-bottom: 1px solid #d6dc20;
}

h6 {
  color: #d6dc20;
}

.border-top-blue {
  border-top: solid 1px #29abe1;
}
button {
  border: 1px solid #d6dc20;
  border-radius: 5px;
  background-color: transparent;
  margin: 0 10px 30px;
  font-family: "Space Mono", sans-serif;
  color: #ffffff;
  font-size: 16px;
  text-align: left;
  z-index: 10;
}
